//CommonService.js

import $ from '../unit/JQueryVendor';
//import layer from '../unit/JqLayer/layer';
//import layer from "../unit/JqLayer/layer/layer";
class CommonService{
    constructor(options) {
        this.options = options || {};
        window.layer = layer;
    }

    
    
    OpenModalLayer() {
        let self = this;
        //
        self.options.header = self.options.header || '';
        self.options.content = self.options.content || '';
        self.options.footer = self.options.footer || '';
        self.options.endFunction = self.options.endFunction || function () { };
        layer.open({
            id: 1,
            //offset: '20%',//
            anim: 0,//
            title: false,//
            type: 1,//
            closeBtn: 0,//
            area: '600px',//
            skin: 'layui-layer-rim',//
            shade: 0.6,//
            shadeClose: true, //
            resize: false,//
            move: false,//
            content: '<div class="modal-content" style="border-radius:0px;">' +
            '<div class="modal-header">' +
            '<button type="button" class="close" onclick="parent.layer.closeAll();">&times;</button>' +
            '<h4 class="modal-title topTitle">' + self.options.header + '</h4>' +
            '</div>' +
            '<div class="modal-body">' + self.options.content +
            '</div>' +
            '<div class="modal-footer">' + self.options.footer +
            '</div>' +
            '</div>',
            end: self.options.endFunction
        });
    }

    //be using
    OpenParentModalLayer() {
        let self = this;
        //
        self.options.header = self.options.header || '';
        self.options.content = self.options.content || '';
        self.options.footer = self.options.footer || '';
        self.options.endFunction = self.options.endFunction || function () { };
        layer.open({
            id: 1,
            offset: ['10%', '33%'],//
            anim: 3,//
            title: false,//
            type: 1,//
            closeBtn: 0,//
            area: '33%',//
            skin: 'layui-layer-rim',//
            shade: 0.6,//
            shadeClose: true, //
            resize: false,//
            move: false,//
            content: '<div class="modal-content" style="border-radius:0px;">' +
            '<div class="modal-header">' +
            '<button type="button" class="close" onclick="parent.layer.closeAll();">&times;</button>' +
            '<h4 class="modal-title topTitle">' + self.options.header + '</h4>' +
            '</div>' +
            '<div class="modal-body">' + self.options.content +
            '</div>' +
            '<div class="modal-footer">' + self.options.footer +
            '</div>' +
            '</div>',
            end: self.options.endFunction
        });
    }
    
    OpenParentModalLayerForDocIndexCtr() {// OpenParentModalLayer shadeClose: false, 
        let self = this;
        //
        self.options.header = self.options.header || '';
        self.options.content = self.options.content || '';
        self.options.footer = self.options.footer || '';
        self.options.endFunction = self.options.endFunction || function () { };
        layer.open({
            id: 1,
            //offset: '20%',//
            anim: 0,//
            title: false,//
            type: 1,//
            closeBtn: 0,//
            area: '600px',//
            skin: 'layui-layer-rim',//
            shade: 0.6,//
            shadeClose: false, //
            resize: false,//
            move: false,//
            content: '<div class="modal-content" style="border-radius:0px;">' +
            '<div class="modal-header">' +
            //'<button type="button" class="close" onclick="parent.layer.closeAll();">&times;</button>' +
            '<h4 class="modal-title topTitle">' + self.options.header + '</h4>' +
            '</div>' +
            '<div class="modal-body">' + self.options.content +
            '</div>' +
            '<div class="modal-footer">' + self.options.footer +
            '</div>' +
            '</div>',
            end: self.options.endFunction
        });
    }
    
    OpenSaveModalLayer() {
        let self = this;
        //
        self.options.header = self.options.header || '';
        self.options.content = self.options.content || '';
        parent.layer.open({
            id: 1,
            //offset: '20%',
            anim: 0,//
            title: false,//
            type: 1,//
            closeBtn: 0,//
            area: '600px',//
            skin: 'layui-layer-rim',//
            shade: 0.6,//
            shadeClose: false, //
            resize: false,//
            move: false,//
            content: '<div class="modal-content">' +
            '<div class="modal-header">' +
            '<button type="button" class="close" onclick="parent.layer.closeAll();">&times;</button>' +
            '<h4 class="modal-title topTitle">' + self.options.header + '</h4>' +
            '</div>' +
            '<div class="modal-body">' + self.options.content +
            '</div>' +
            '<div class="modal-footer">' +
            '<button type="button" class="btn btn-default CloseLayer" onclick="parent.layer.closeAll();"></button>' +
            '<button type="submit" class="btn btn-primary"></button>' +
            '</div>' +
            '</div>'
        });
        $('.btn.btn-primary', window.parent.document).click(function () {
            parent.layer.closeAll();
            self.options.Submit(this);
        });
    }

    OpenConfirmModalLayer() {
        let self = this;
        parent.layer.open({
            id: 1,
            anim: 0,//
            title: false,//
            type: 1,//
            closeBtn: 0,//
            area: '600px',//
            skin: 'layui-layer-rim',//
            shade: 0.6,//
            shadeClose: false, //
            resize: false,//
            move: false,//
            content: '<div class="modal-content">' +
            '<div class="modal-header">' +
            '<button type="button" class="close" onclick="parent.layer.closeAll();">&times;</button>' +
            '<h4 class="modal-title topTitle">' + self.options.header + '</h4>' +
            '</div>' +
            '<div class="modal-body">' + self.options.content +
            '</div>' +
            '<div class="modal-footer">' +
            '<button type="button" class="btn btn-default" onclick="parent.layer.closeAll();"></button>' +
            '<button type="button" id="btnConfirm" class="btn btn-default"></button>' +
            '</div>' +
            '</div>'
        })
        let confirmctr = $("#btnConfirm");
        if (confirmctr[0] == undefined) {
            confirmctr = $("#btnConfirm", window.parent.document);
        }
        confirmctr.bind('click', () => {
            if (self.options.Confirm != undefined)
                self.options.Confirm.call(this);
        });
    }
    
    Loading() {
        let self = this;
        self.index = parent.layer.load(1, {
            shade: [0.1, '#fff'] //0.1
        });
    }
    
    LoadingOpaque() {
        let self = this;
        self.index = parent.layer.load(1, {
            shade: [1, '#fff'] //1()
        });
    }

    CloseLoading(){
        let self = this;
        parent.layer.close(self.index);
    }

    //1  2 /  3 4 5
    AlertMessage(message, iconNum) {
        let self = this;
        self.options.iconNum = iconNum || 1;
        parent.layer.alert(message, {
            icon: self.options.iconNum,
            skin: 'layer-ext-moon' //layer.seaning.com
        });
    }

    PromptMsg(message, iconNum) {
        let self = this;
        self.options.iconNum = iconNum || 1;
        parent.layer.msg(message, {
            icon: self.options.iconNum,
            time: 5000 //33
        }, function () {
            //do something
        });
    }

    CloseLayer() {
        parent.layer.closeAll();
    }
	
    _generateNavi(userInfo,active){
    	let self = this;
    	if(userInfo.priv!="ADMINISTRATOR" && userInfo.priv!="OPERATOR"){
    		return;
    	}

    	let html = '	<!--nav-->'
		+'<nav class="navbar navbar-default" role="navigation">'
			+'<div class="container-fluid">'
			+'<div class="navbar-header">'
				+'<a class="navbar-brand" href="/gd"></a>'
			+'</div>'
			+'<div>'
				+'<ul class="nav navbar-nav">'
					+'<li '
					+ active[0]
					+' ><a  style="border-left:1px solid;"href="/gd"></a></li>'
					
/*					+'<li class="dropdown">'
						+'<a href="#" class="dropdown-toggle" data-toggle="dropdown">'
							+''
							+'<b class="caret"></b>'
						+'</a>'
						+'<ul class="dropdown-menu">'*/
							if(userInfo.priv=="OPERATOR"){
								html += '<li '
									+active[1]
									+' ><a href="/gd/chartdata/wholeState"></a></li>'
							}
    						html += '<li '
    							+active[2]
								+' ><a href="/gd/chartdata/wholeWholeState"></a></li>'
							
							
    	
/*						html += '</ul>'
					+'</li>'*/
					
					if(userInfo.priv=="ADMINISTRATOR"){
						html +='<li '
							+active[3]
							+' ><a  href="/gd/data/export"></a></li>'
					}
				html +='</ul>'
					+'<ul class="nav navbar-nav navbar-right">'
					+'<li class="dropdown" >'
					+'<a  style="border-left:1px solid;width:160px" href="#" class="dropdown-toggle" data-toggle="dropdown"><span class="glyphicon glyphicon-user"></span>&nbsp;&nbsp;'
					+ userInfo.userName
					+'<b class="caret"></b>'
	                +'</a>'
	                +'<ul class="dropdown-menu">'
	                +'<li><a style="cursor:pointer" id="accountinfo"><span class="glyphicon glyphicon-info-sign"></span>&nbsp;&nbsp;</a></li>'
	                +'<li class="divider"></li>'
	                +'<li><a style="cursor:pointer" id="changepwd"><span class="glyphicon glyphicon-pencil"></span>&nbsp;&nbsp;</a></li>'
	                +'<li class="divider"></li>'
	                +'<li><a style="cursor:pointer" id="logout"><span class="glyphicon glyphicon-log-out"></span>&nbsp;&nbsp;</a></li>'
	                +'</ul>'
	                +'</li>'
	            +'</ul>'
			+'</div>'
			+'</div>'
		+'</nav><!--nav-->'
		$("#navi").css("height","50px");
    	$("#navi").html(html);
		
		$("#accountinfo").bind("click",function(){
			alert("not implement yet");
		});	
		$("#logout").bind("click",function(){
			//alert("not implement yet");
			window.location.href = "/gd/user/logout";			
		});	
		$("#changepwd").bind("click",function(){
			console.log("not implement yet");
			self.options={
						header: '',
			            content://'<form role="form" id="addStation" method="POST" action="/gd/station/add">' +
		                    //'<div class="form-group">' +
			            	'<div>'+
		                    '<label id="msg_ori" for="name"></label>' +
		                    '<input type="password" class="form-control" id="oriPwd" name="oriPwd" placeholder="">' +
		                    '</div>'+
			            	'<div>'+
		                    '<label for="name"></label>' +
		                    '<input type="password" class="form-control" id="newPwd" name="newPwd" placeholder="">' +
		                    '</div>'+
			            	'<div>'+
		                    '<label id="msg_new" for="name"></label>' +
		                    '<input type="password" class="form-control" id="confPwd" name="confPwd" placeholder="">'+
		                    '</div>',
		                    //'<label for="name"></label>' +
		                    //'<input type="text" class="form-control" id="brief" name="brief" placeholder="">' +
		                    
			                //'<input type="hidden" class="form-control" id="imgPath" value="http://pic34.photophoto.cn/20150330/0007019952833279_b.jpg" name="imgPath">' +
			                //'<input type="hidden" id="posx" name="posx" value="'+posx+'"/>' +
			                //'<input type="hidden" id="posy" name="posy" value="'+posy+'"/>' +
		                    //'</div>'+
		                    //' </form>',
		            footer: '<button type="button" class="btn btn-default pwdcancel"></button>' +
		                    '<button type="button" class="btn btn-primary pwdsubmit"></button>' 
		                    
		        };
			self.OpenParentModalLayer();
			$('#oriPwd').on('blur',function(){
				console.log($('#oriPwd').val());
				self._confOriPwd($('#oriPwd').val(),userInfo);
			});
			$('.pwdcancel').click(function(){
				self.CloseLayer();
			});
			$('.pwdsubmit').click(function(){
				$('#msg_ori').html('');
				$('#msg_new').html('');
				if($('#oriPwd').val()==""){
					console.log('original password empty');
					$('#msg_ori').html('&nbsp&nbsp<small style="color:red">*</small>');
				}else if($('#newPwd').val() == ""){
					$('#msg_new').html('&nbsp&nbsp<small style="color:red">*</small>');
				}else if($('#newPwd').val() != $('#confPwd').val()){
					console.log('new password diff');
					$('#msg_new').html('&nbsp&nbsp<small style="color:red">*</small>');
				}else{
					let oriPwd = $('#oriPwd').val();
					$.ajax({
						type : "POST",
							url : "/gd/user/confirmPwd",
							traditional : true,
							dataType : "text",
							data : {userId:userInfo.userId,oriPwd:oriPwd},
							beforeSend : function(){},
							success : function(data) {
								console.log(data);
								if(data=='mismatch'){
									$('#msg_ori').html('&nbsp&nbsp<small style="color:red">*</small>');
								}else{
									$('#msg_ori').html('');
									let newPwd = $('#newPwd').val();
									$.ajax({
										type : "POST",
											url : "/gd/user/modifyPwd",
											traditional : true,
											dataType : "text",
											data : {userId:userInfo.userId,newPwd:newPwd},
											beforeSend : function(){},
											success : function(data) {
												console.log(data);
												if(data=='success'){
													alert('');
													location.href="/gd/user/login";
												}else{
													location.href="/gd/user/login";
												}
											},
											error : function(){},
											complete : function(){
												self.CloseLayer();
											},					
									});
								}
							},
							error : function(){},
							complete : function(){},					
					});
				}
				
			});
			
		});	
				
    }
    _confOriPwd(oriPwd,userInfo){
    	console.log(userInfo);
    	console.log(userInfo.userId);
    	$.ajax({
			type : "POST",
				url : "/gd/user/confirmPwd",
				traditional : true,
				dataType : "text",
				data : {"userId":userInfo.userId,"oriPwd":oriPwd},
				beforeSend : function(){},
				success : function(data) {
					console.log(data);
					if(data=='mismatch'){
						//alert('pwd mismatch');
						$('#msg_ori').html('&nbsp&nbsp<small style="color:red">*</small>');
					}else{
						$('#msg_ori').html('');
					}
				},
				error : function(){},
				complete : function(){},					
		});
    }
}
export default CommonService;


